<template>
  <div class="blog-post upper-accent" style="padding: 5vmin 10vmin 5vmin 10vmin;">
    <v-row no-gutters="" align="center" justify="center">
      <v-col xl="8" lg="9" md="10" sm="11" cols="12">
        <p :class="changeDescClasses + ' mb-4'"> {{ title }} </p>
        <img :src="postImage" width="100%">
        <div class="py-6" v-html="post">

        </div>

        <v-row align="center" justify="start">
          <v-chip
            :class="bodyClasses + ' ma-3'"
            v-for="tag in tags" :key="tag"
            label :large="$vuetify.breakpoint.lgAndUp"
          >
            <span
              class="font-weight-medium grey--text text--darken-2" style="font-size: 0.75em;"
            >
              {{ tag }}
            </span>
          </v-chip>
        </v-row>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import moment from 'moment';
import TestImage from '../assets/images/banner_image2.png';

export default {
  data: () => ({
    title: 'This Is A Test Title',
    post: 'Test',
    postImage: TestImage,
    tags: ['These', 'Are Going', 'To Be', 'Tags, Ok?'],
  }),
  computed: {
    titleSize() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'title';
        case 'sm':
          return 'headline';
        case 'md':
          return 'display-1';
        case 'lg':
          return 'display-2';
        default:
          return 'display-2';
      }
    },

    bannerClasses() {
      const baseClasses = 'primary white--text display-2 text-center banner-text font-weight-black';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} ${this.titleSize} pa-0`;
        case 'sm':
          return `${baseClasses} ${this.titleSize} pa-2`;
        case 'md':
          return `${baseClasses} ${this.titleSize} pa-3 mb-8`;
        case 'lg':
          return `${baseClasses} ${this.titleSize} pa-4 mb-12`;
        default:
          return `${baseClasses} ${this.titleSize} pa-4 mb-12`;
      }
    },

    changeDescClasses() {
      const baseClasses = 'font-weight-bold grey--text text--darken-4 pa-0 ma-0';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} body-1`;
        case 'sm':
          return `${baseClasses} title`;
        case 'md':
          return `${baseClasses} headline`;
        case 'lg':
          return `${baseClasses} display-1`;
        default:
          return `${baseClasses} display-1`;
      }
    },

    bodyClasses() {
      const baseClasses = 'grey--text';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} body-2`;
        case 'sm':
          return `${baseClasses} body-1`;
        case 'md':
          return `${baseClasses} title`;
        case 'lg':
          return `${baseClasses} headline`;
        default:
          return `${baseClasses} headline`;
      }
    },

    changeSubDescPadding() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'px-5 py-1';
        case 'sm':
          return 'px-7 py-2';
        case 'md':
          return 'px-9 py-3';
        case 'lg':
          return 'px-11 py-4';
        default:
          return 'px-11 py-4';
      }
    },

    avatarSize() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 70;
        case 'sm':
          return 90;
        case 'md':
          return 110;
        case 'lg':
          return 130;
        default:
          return 150;
      }
    },

    avatarStyle() {
      return `min-width: ${this.avatarSize}px; max-width: ${this.avatarSize}px; min-height: ${this.avatarSize}px; max-height: ${this.avatarSize}px;`;
    },

    postDate() {
      return moment(new Date(this.date)).format('MMMM Do, YYYY');
    },
  },
};
</script>

<style scoped>
.underline {
  text-decoration: underline !important;
  text-decoration-color: #FFE52D !important;
}

div .upper-accent {
  border-width: 15px 0px 0px 0px;
  border-top-color: #FFE52D !important;
  border-top-style: solid;
}
</style>
