<template>
  <div class="who-we-serve upper-accent" style="padding: 5vmin 10vmin 5vmin 10vmin;">
    <div :class="titlePadding">
      <p
        :class="`underline text-center font-weight-bold ma-0 ${titleSize}`"
      >
        CASE STUDIES
      </p>
    </div>

    <v-row no-gutters="" align="center" justify="center">
      <v-col xl="8" lg="9" md="10" sm="11" cols="12">
        <v-row v-for="i in 4" :key="i" no-gutters="" align="center" justify="center">
          <study-card
            title="Study Card Title"
            description="Study Card Description"
            :postImage="tImg"
            studyLink="#/case-study"
            :tags="tTags"
            :flip="i % 2 == 0"
          ></study-card>
          <div v-if="i < 4" style="width: 100%; height: 5px;" class="primary ma-6"></div>
        </v-row>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import StudyCard from '../components/StudyCard.vue';
import TestImage from '../assets/images/57308670_529055400831799_1159447484074819584_o.jpg';

export default {
  data: () => ({
    tImg: TestImage,
    tTags: ['Look', 'At All', 'These', 'Tags'],
  }),
  computed: {
    titleSize() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'title';
        case 'sm':
          return 'headline';
        case 'md':
          return 'display-1';
        case 'lg':
          return 'display-2';
        default:
          return 'display-2';
      }
    },

    titlePadding() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'pb-3';
        case 'sm':
          return 'pb-5';
        case 'md':
          return 'pb-7';
        case 'lg':
          return 'pb-9';
        default:
          return 'pb-9';
      }
    },

    masonGutter() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return '10px';
        case 'sm':
          return '15px';
        case 'md':
          return '20px';
        case 'lg':
          return '25px';
        default:
          return '30px';
      }
    },

    cardCols() {
      return this.$vuetify.breakpoint.smAndDown ? 2 : 3;
    },
  },

  components: { StudyCard },
};
</script>

<style scoped>
.underline {
  text-decoration: underline !important;
  text-decoration-color: #FFE52D !important;
}

div .upper-accent {
  border-width: 15px 0px 0px 0px;
  border-top-color: #FFE52D !important;
  border-top-style: solid;
}
</style>
